
.title{
    margin: 3%;
}

/* posicionamiento */

.accordion {
	margin: auto;
	width: 400px;
}

/* cerrar cajas */

.accordion input {
	display: none;
}

/* cajas 1-2 */

.box {
	position: relative;
	background: white;
    height: 100%;
	transition: all .15s ease-in-out;

}

.text{
	margin: 3%;

	border-radius: 5px;
	background-color: white;
	padding: 1%;
  }

/* vista caja cerrada */

.box::before {
    content: '';
    position: absolute;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    pointer-events: none;
	box-shadow: 0 -1px 0 #e5e5e5,0 0 2px rgba(0,0,0,.12),0 2px 4px rgba(0,0,0,.24);
	
}

/* caja enunciado */

header.box {
	background: rgb(6, 92, 104);
	z-index: 100;
	cursor: initial;
	box-shadow: 0 -1px 0 #e5e5e5,0 0 2px -2px rgba(0,0,0,.12),0 2px 4px -4px rgba(0,0,0,.24);
}

/* efecto 3d caja enunciado */

header .box-title {
	margin: 0;
	font-weight: normal;
	font-size: 16pt;
	color: white;
	cursor: initial;
}



.box-title {
	width: calc(100% - 40px);
	height: 64px;
	line-height: 64px;
	padding: 0 20px;
	display: inline-block;
	cursor: pointer;
	-webkit-touch-callout: none;-webkit-user-select: none;-khtml-user-select: none;-moz-user-select: none;-ms-user-select: none;user-select: none;
}

/* distribucion texto interno */

.box-content {
	width: calc(100% - 40px);
	padding: 30px 20px;
	font-size: 11pt;
	color: rgba(0,0,0,.54);
	display: none;
}

/* clickar para cerrar */
.box-close {
	position: absolute;
	height: 64px;
	width: 100%;
	top: 0;
	left: 0;
	cursor: pointer;
	display: none;
}

/* texto interno */

input:checked + .box {
	height: auto;
	margin: 16px 0;
	box-shadow: 0 0 6px rgba(0,0,0,.16),0 6px 12px rgba(0,0,0,.32);
	
}

/* linea separacion interna */

input:checked + .box .box-title {
	border-bottom: 1px solid rgba(0,0,0,.18);
}

/* abre */

input:checked + .box .box-content,
input:checked + .box .box-close {
	display: inline-block;
}
